@import "../colors.scss";
@import "../sizings.scss";

.labeled-list {
  display: grid;
  grid-template-columns: max-content 1fr;
  column-gap: $large-size;
  row-gap: $small-size;
  margin: $large-size 0;

  .title {
    font-weight: bold;
    grid-column-end: span 2;
  }
  .label {
    font-weight: bold;
  }
}

.grid-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(5em, 1fr));
  column-gap: $large-size;
  row-gap: $small-size;
  justify-items: center;
  margin: $large-size 0;
}

.widget-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: $small-size;
  margin: $large-size 0;
}
